.Landing::before {
	position: absolute;
	top: -4px;
	left: -4px;
	bottom: -4px;
	right: -4px;
	content: "";
	background-color: #444;
	background-image: url("./images/landing.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	z-index: -1;
	-webkit-filter: blur(2px) brightness(0.55) grayscale(50%);
	        filter: blur(2px) brightness(0.55) grayscale(50%);
}

.Landing {
	min-height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: end;
	    justify-content: end;
}

.Landing-title-section {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.Landing-title {
	font-size: 6rem;
	font-family: "Lobster", cursive;
	letter-spacing: 0.3rem;
	-webkit-transform: translateX(calc(-50vw - 50%));
	    -ms-transform: translateX(calc(-50vw - 50%));
	        transform: translateX(calc(-50vw - 50%));
	-webkit-animation: slide-in 1.5s 8s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
	        animation: slide-in 1.5s 8s 1 cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
	will-change: transform;
}

.Landing-subtitle {
	font-size: 2.5rem;
	letter-spacing: 0.12em;
}

.Landing-subtitle p {
	margin: 0;
	display: inline-block;
	overflow: hidden;
	white-space: nowrap;
}

.Landing-subtitle:nth-child(1) {
	width: 267px;
}

.Landing-subtitle:nth-child(2) {
	width: 764px;
}

.Landing-subtitle_line1 {
	border-right: 0.08em solid #ccc;
	-webkit-animation: typing 3s steps(20, end), blink-caret 0.5s step-end infinite forwards, hide-cursor 0s 3s 1 forwards;
	        animation: typing 3s steps(20, end), blink-caret 0.5s step-end infinite forwards, hide-cursor 0s 3s 1 forwards;
}

.Landing-subtitle_line2 {
	width: 0;
	border-right: 0.08em solid transparent;
	-webkit-animation: typing 5s 3s steps(40, end) forwards, blink-caret 0.5s 3s step-end infinite, hide-cursor 0s 8s 1 forwards;
	        animation: typing 5s 3s steps(40, end) forwards, blink-caret 0.5s 3s step-end infinite, hide-cursor 0s 8s 1 forwards;
}

.Landing-check-section {
	height: 8rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.Landing-check-label {
	text-transform: uppercase;
	cursor: pointer;
	height: 3rem;
	color: #ccc;
	-webkit-transition: color linear 0.2s;
	-o-transition: color linear 0.2s;
	transition: color linear 0.2s;
}

.Landing-check-label:hover {
	color: #fff;
}

.scroll-down-wrapper {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	cursor: pointer;
	position: relative;
}

.scroll-down-line {
	width: 0.1rem;
	height: 5rem;
	background-color: #fff;
}

.scroll-down-marker {
	width: 0.4rem;
	height: 1.5rem;
	position: absolute;
	background-color: #fff;
	top: 0;
	-webkit-animation: scroll-down 3s 1s cubic-bezier(0.18, 0.89, 0.71, 0.9) infinite;
	        animation: scroll-down 3s 1s cubic-bezier(0.18, 0.89, 0.71, 0.9) infinite;
}

@-webkit-keyframes scroll-down {
	from {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
	to {
		-webkit-transform: translateY(5rem);
		        transform: translateY(5rem);
	}
}

@keyframes scroll-down {
	from {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
	to {
		-webkit-transform: translateY(5rem);
		        transform: translateY(5rem);
	}
}

/* typing effect */
@-webkit-keyframes typing {
	from {
		width: 0;
	}
	to {
		width: 100%;
	}
}
@keyframes typing {
	from {
		width: 0;
	}
	to {
		width: 100%;
	}
}

@-webkit-keyframes hide-cursor {
	to {
		border: 0;
	}
}

@keyframes hide-cursor {
	to {
		border: 0;
	}
}

/* typewriter cursor effect */
@-webkit-keyframes blink-caret {
	from,
	to {
		border-color: transparent;
	}
	50% {
		border-color: #ccc;
	}
}
@keyframes blink-caret {
	from,
	to {
		border-color: transparent;
	}
	50% {
		border-color: #ccc;
	}
}

@-webkit-keyframes slide-in {
	to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@keyframes slide-in {
	to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}



