.About {
	min-height: 140vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	background-color: #1c1c1c;
	z-index: 1;
}

.About.more {
	min-height: 120vh;
}

.About-header {
	padding: 1.5rem 10% 2rem;
	cursor: pointer;
	font-size: 3.5rem;
	-ms-flex-align: end;
	    align-items: flex-end;
	color: #ddd;
}

.About-header-more {
	border-bottom: 1px solid #ddd;
	padding-right: 1.2rem;
	padding-left: 2rem;
}

.About-header-me {
	background-color: #1c1c1c;
	-webkit-transform: translateX(-15rem);
	    -ms-transform: translateX(-15rem);
	        transform: translateX(-15rem);
	-webkit-transition: -webkit-transform 0.45s ease-out;
	transition: -webkit-transform 0.45s ease-out;
	-o-transition: transform 0.45s ease-out;
	transition: transform 0.45s ease-out;
	transition: transform 0.45s ease-out, -webkit-transform 0.45s ease-out;
	will-change: transform;
	border-bottom: 1px solid;
	padding-right: 2rem;
}

.more .About-header-me {
	-webkit-transform: translateX(0);
	    -ms-transform: translateX(0);
	        transform: translateX(0);
}

.arrow {
	width: 0;
	height: 0;
}

.arrow__left {
	border-bottom: 0.6rem solid #fff;
	border-left: 1.2rem solid transparent;
	opacity: 0;
}

.arrow__right {
	border-bottom: 0.6rem solid #fff;
	border-right: 1.2rem solid transparent;
	-webkit-animation: move-right 5s cubic-bezier(0.45, 0.05, 0.9, 1) infinite;
	        animation: move-right 5s cubic-bezier(0.45, 0.05, 0.9, 1) infinite;
}

.more .arrow__right {
	-webkit-animation: none;
	        animation: none;
	opacity: 0;
}

.more .arrow__left {
	opacity: 1;
}

.About-inner {
	position: relative;
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	overflow: hidden;
}

.more .parallax-1,
.more .parallax-2 {
	-webkit-transform: translateX(calc(50vw + 50%));
	    -ms-transform: translateX(calc(50vw + 50%));
	        transform: translateX(calc(50vw + 50%));
}

.more .About-section__quote {
	-webkit-transform: translateX(calc(-50vw - 50%));
	    -ms-transform: translateX(calc(-50vw - 50%));
	        transform: translateX(calc(-50vw - 50%));
}

.About-more-container {
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	top: 0;
	left: 0;
	bottom: 0;
	width: 100%;
	z-index: -1;
	background-color: #212121;
	padding: 2rem 0;
}

.About-more-text {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: end;
	    align-items: flex-end;
	padding: 3rem 4rem 0 0;
	visibility: hidden;
}

.About-more-text__quote {
	color: #888;
	margin: 0;
	max-width: 30vw;
}

.About-more-text__quote p:first-child {
	font-family: 'Playfair Display', serif;
	font-size: 1.2rem;
}

.About-more-text__quote p:last-child {
	text-align: right;
}

.About-more-text-wrapper {
	overflow: hidden;
	margin: 1rem 0;
}

.About-more-text-wrapper p {
	color: #888;
	line-height: 1.6;
	margin: 0;
	max-width: 30vw;
	font-size: 1.2rem;
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
	will-change: transform;
}

.About-more-text .About-more-text-wrapper:nth-child(1) p {
	-webkit-transition: -webkit-transform 0.5s ease-in-out 1s;
	transition: -webkit-transform 0.5s ease-in-out 1s;
	-o-transition: transform 0.5s ease-in-out 1s;
	transition: transform 0.5s ease-in-out 1s;
	transition: transform 0.5s ease-in-out 1s, -webkit-transform 0.5s ease-in-out 1s;
}

.About-more-text .About-more-text-wrapper:nth-child(2) p {
	-webkit-transition: -webkit-transform 0.5s ease-in-out 1.5s;
	transition: -webkit-transform 0.5s ease-in-out 1.5s;
	-o-transition: transform 0.5s ease-in-out 1.5s;
	transition: transform 0.5s ease-in-out 1.5s;
	transition: transform 0.5s ease-in-out 1.5s, -webkit-transform 0.5s ease-in-out 1.5s;
}

.About-more-text .About-more-text-wrapper:nth-child(3) p {
	-webkit-transition: -webkit-transform 0.5s ease-in-out 2s;
	transition: -webkit-transform 0.5s ease-in-out 2s;
	-o-transition: transform 0.5s ease-in-out 2s;
	transition: transform 0.5s ease-in-out 2s;
	transition: transform 0.5s ease-in-out 2s, -webkit-transform 0.5s ease-in-out 2s;
}

.About-more-text .About-more-text-wrapper:nth-child(4) p {
	-webkit-transition: -webkit-transform 0.5s ease-in-out 2.5s;
	transition: -webkit-transform 0.5s ease-in-out 2.5s;
	-o-transition: transform 0.5s ease-in-out 2.5s;
	transition: transform 0.5s ease-in-out 2.5s;
	transition: transform 0.5s ease-in-out 2.5s, -webkit-transform 0.5s ease-in-out 2.5s;
}

.more .About-more-text {
	visibility: visible;
}

.more .About-more-text-wrapper p {
	-webkit-transform: translateY(0);
	    -ms-transform: translateY(0);
	        transform: translateY(0);
}

.About-more-photos-wrapper {
	-ms-flex: 1 1;
	    flex: 1 1;
	height: 85%;
	visibility: hidden;
}

.About-more-photos {
	position: relative;
	width: 70%;
	max-width: 33rem;
	height: 100%;
	overflow: hidden;
}

.more .About-more-photos-wrapper {
	visibility: visible;
}

.About-more-photos::after {
	content: '';
	background-color: #212121;
	position: absolute;
	height: calc(100% / 2.5);
	width: calc(100% / 11.5);
	z-index: 5;
}

.About-more-photos::before {
	content: '';
	background-color: #212121;
	position: absolute;
	height: calc(100% / 3.286);
	width: calc(100% / 3.286);
	right: 0;
	z-index: 5;
}

.About-more-photos li {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	position: absolute;
	width: 100%;
	height: 100%;
	-webkit-transition: -webkit-transform 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
	transition: -webkit-transform 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
	-o-transition: transform 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
	transition: transform 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
	transition: transform 1s cubic-bezier(0.55, 0.33, 0.01, 1.01), -webkit-transform 1s cubic-bezier(0.55, 0.33, 0.01, 1.01);
	will-change: transform;
}

.About-more-photos li:nth-child(1) {
	background-image: url('./images/show1.png');
	-webkit-filter: brightness(0.8) grayscale(50%);
	        filter: brightness(0.8) grayscale(50%);
}

.About-more-photos li:nth-child(2) {
	background-image: url('./images/show2.png');
	-webkit-filter: brightness(0.8) grayscale(40%);
	        filter: brightness(0.8) grayscale(40%);
}

.About-more-photos li:nth-child(3) {
	background-image: url('./images/show3.png');
	-webkit-filter: brightness(0.8) grayscale(80%);
	        filter: brightness(0.8) grayscale(80%);
}

.About-more-photos li:nth-child(4) {
	background-image: url('./images/show4.png');
	-webkit-filter: brightness(0.8) grayscale(80%);
	        filter: brightness(0.8) grayscale(80%);
}

.About-more-photos .current {
	-webkit-transform: translateX(0);
	    -ms-transform: translateX(0);
	        transform: translateX(0);
	z-index: 1;
}

.About-more-photos .pre {
	-webkit-transform: translateX(-100%);
	    -ms-transform: translateX(-100%);
	        transform: translateX(-100%);
	z-index: 0;
}

.About-more-photos .after {
	-webkit-transform: translateX(100%);
	    -ms-transform: translateX(100%);
	        transform: translateX(100%);
}

.About-more-photos .mask {
	position: absolute;
	background-color: #1c1c1c;
	opacity: 0.55;
	z-index: 5;
	-webkit-transition: -webkit-transform 0.5s ease-in-out 1s;
	transition: -webkit-transform 0.5s ease-in-out 1s;
	-o-transition: transform 0.5s ease-in-out 1s;
	transition: transform 0.5s ease-in-out 1s;
	transition: transform 0.5s ease-in-out 1s, -webkit-transform 0.5s ease-in-out 1s;
	will-change: transform;
}

.About-more-photos .mask.mask__shown {
	-webkit-transform: translateX(0);
	    -ms-transform: translateX(0);
	        transform: translateX(0);
}

.About-more-photos .mask0 {
	left: 0;
	top: calc(100% / 2.5);
	height: calc(100% - 100% / 2.5);
	width: 40%;
	-webkit-transform: translateX(-100%);
	    -ms-transform: translateX(-100%);
	        transform: translateX(-100%);
}

.About-more-photos .mask1 {
	left: 0;
	top: calc(100% / 1.5);
	height: calc(100% - 100% / 1.5);
	width: 100%;
	-webkit-transform: translateX(-100%);
	    -ms-transform: translateX(-100%);
	        transform: translateX(-100%);
}

.About-more-photos .mask2 {
	right: 0;
	top: calc(100% / 3.286);
	height: calc(100% / 3.286);
	width: calc(100% / 3.286);
	-webkit-transform: translateX(100%);
	    -ms-transform: translateX(100%);
	        transform: translateX(100%);
}

.About-more-photos .mask3 {
	left: 0;
	top: 0;
	height: calc(100% / 2.5);
	width: 100%;
	-webkit-transform: translateX(-100%);
	    -ms-transform: translateX(-100%);
	        transform: translateX(-100%);
}

.About-section {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	position: relative;
	-webkit-transform: translateX(0);
	    -ms-transform: translateX(0);
	        transform: translateX(0);
	-webkit-transition: -webkit-transform 0.8s ease-out;
	transition: -webkit-transform 0.8s ease-out;
	-o-transition: transform 0.8s ease-out;
	transition: transform 0.8s ease-out;
	transition: transform 0.8s ease-out, -webkit-transform 0.8s ease-out;
	will-change: transform;
}

.About-section p {
	z-index: 2;
	width: 70vw;
	font-size: 1.5rem;
}

.About-section__quote {
	font-style: italic;
	margin: 0;
	height: 20vh;
	background-color: #212121;
}

.About-section__quote p {
	font-size: 1.2rem;
	margin: 0;
}

.About-section__quote p:first-child {
	font-family: 'Playfair Display', serif;
}

.About-section__quote p:last-child {
	text-align: right;
}

.About-section-parallax {
	width: 100%;
	height: 100%;
	position: absolute;
	background-attachment: fixed;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	-webkit-filter: brightness(0.45) grayscale(50%);
	        filter: brightness(0.45) grayscale(50%);
}

.parallax-1 {
	height: 40vh;
}

.parallax-1 .About-section-parallax {
	background-image: url('./images/coding1.jpg');
}

.parallax-2 {
	height: 60vh;
}

.parallax-2 .About-section-parallax {
	background-image: url('./images/coding2.jpg');
}

@-webkit-keyframes move-right {
	0% {
		-webkit-transform: translateX(-40rem);
		        transform: translateX(-40rem);
	}
	40%,
	100% {
		-webkit-transform: translateX(-15rem);
		        transform: translateX(-15rem);
	}
}

@keyframes move-right {
	0% {
		-webkit-transform: translateX(-40rem);
		        transform: translateX(-40rem);
	}
	40%,
	100% {
		-webkit-transform: translateX(-15rem);
		        transform: translateX(-15rem);
	}
}



